import { useState, useMemo, useEffect } from "react";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import * as z from "zod";
import { useTranslation } from "react-i18next";
import { Link, useSearchParams } from "react-router-dom";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Checkbox } from "@/components/ui/checkbox";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Form, FormControl, FormDescription, FormField, FormItem, FormLabel, FormMessage } from "@/components/ui/form";
import { supabase } from "@/integrations/supabase/client";
import { toast } from "@/hooks/use-toast";
import { 
  CheckCircle2, Loader2, Store, Phone, Mail, Ticket, Check, X, ExternalLink, 
  ChevronRight, ChevronLeft, HelpCircle, Ghost, Building2, User, MapPin
} from "lucide-react";
import { useSiteSettings } from "@/hooks/useSiteSettings";
import { useValidateInvitationCode } from "@/hooks/useInvitationCodes";
import { openExternalUrl } from "@/lib/externalLinks";
import { cn } from "@/lib/utils";
import { Progress } from "@/components/ui/progress";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";

const featureOptions = ["booking", "delivery", "qrcode"] as const;

type RestaurantType = "standard" | "ghost";

type FormValues = {
  restaurant_type: RestaurantType;
  name: string;
  email: string;
  phone: string;
  restaurant_name: string;
  restaurant_address: string;
  features_selected: typeof featureOptions[number][];
  invitation_code: string;
};

const STEPS = [
  { id: 1, key: "type" },
  { id: 2, key: "contact" },
  { id: 3, key: "restaurant" },
  { id: 4, key: "features" },
] as const;

const JoinUs = () => {
  const { t, i18n } = useTranslation();
  const [searchParams] = useSearchParams();
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [isSuccess, setIsSuccess] = useState(false);
  const [currentStep, setCurrentStep] = useState(1);
  const [showGhostInfo, setShowGhostInfo] = useState(false);
  const { settings } = useSiteSettings();
  const validateCodeMutation = useValidateInvitationCode();
  const [codeValidation, setCodeValidation] = useState<{
    isValidating: boolean;
    isValid: boolean | null;
    inviterUserId: string | null;
  }>({ isValidating: false, isValid: null, inviterUserId: null });

  const formSchema = useMemo(() => z.object({
    restaurant_type: z.enum(["standard", "ghost"]),
    name: z.string().min(2, { message: t("joinUs.nameMin") }),
    email: z.string().email({ message: t("joinUs.emailInvalid") }),
    phone: z.string().min(6, { message: t("joinUs.phoneMin") }),
    restaurant_name: z.string().min(2, { message: t("joinUs.restaurantNameMin") }),
    restaurant_address: z.string().min(2, { message: t("joinUs.addressMin") }),
    features_selected: z.array(z.enum(featureOptions)).min(1, { message: t("joinUs.featuresMin") }),
    invitation_code: z.string().optional(),
  }), [t]);

  const form = useForm<FormValues>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      restaurant_type: "standard",
      name: "",
      email: "",
      phone: "",
      restaurant_name: "",
      restaurant_address: "",
      features_selected: [],
      invitation_code: searchParams.get("code") || "",
    },
  });

  const restaurantType = form.watch("restaurant_type");
  const featuresSelected = form.watch("features_selected");
  const isQrcodeSelected = featuresSelected?.includes("qrcode");
  const isGhostRestaurant = restaurantType === "ghost";

  // Calculate total steps based on restaurant type
  // Ghost restaurants skip the features step (step 4)
  const totalSteps = isGhostRestaurant ? 3 : 4;

  // Validate invitation code from URL on mount
  useEffect(() => {
    const codeFromUrl = searchParams.get("code");
    if (codeFromUrl && isQrcodeSelected && !isGhostRestaurant) {
      handleValidateCode(codeFromUrl);
    }
  }, [searchParams, isQrcodeSelected, isGhostRestaurant]);

  // Clear invitation code when qrcode is deselected or ghost restaurant selected
  useEffect(() => {
    if (!isQrcodeSelected || isGhostRestaurant) {
      form.setValue("invitation_code", "");
      setCodeValidation({ isValidating: false, isValid: null, inviterUserId: null });
    }
  }, [isQrcodeSelected, isGhostRestaurant, form]);

  // Auto-set features for ghost restaurant
  useEffect(() => {
    if (isGhostRestaurant) {
      form.setValue("features_selected", ["delivery"]);
    }
  }, [isGhostRestaurant, form]);

  const handleValidateCode = async (code: string) => {
    if (!code || code.length < 6) {
      setCodeValidation({ isValidating: false, isValid: null, inviterUserId: null });
      return;
    }

    setCodeValidation({ isValidating: true, isValid: null, inviterUserId: null });
    
    try {
      const result = await validateCodeMutation.mutateAsync(code);
      if (result) {
        setCodeValidation({ 
          isValidating: false, 
          isValid: true, 
          inviterUserId: result.user_id 
        });
      } else {
        setCodeValidation({ isValidating: false, isValid: false, inviterUserId: null });
      }
    } catch {
      setCodeValidation({ isValidating: false, isValid: false, inviterUserId: null });
    }
  };

  const validateCurrentStep = async (): Promise<boolean> => {
    let fieldsToValidate: (keyof FormValues)[] = [];
    
    switch (currentStep) {
      case 1:
        fieldsToValidate = ["restaurant_type"];
        break;
      case 2:
        fieldsToValidate = ["name", "email", "phone"];
        break;
      case 3:
        fieldsToValidate = ["restaurant_name", "restaurant_address"];
        break;
      case 4:
        if (!isGhostRestaurant) {
          fieldsToValidate = ["features_selected"];
        }
        break;
    }
    
    const result = await form.trigger(fieldsToValidate);
    return result;
  };

  const handleNext = async (e: React.MouseEvent<HTMLButtonElement>) => {
    e.preventDefault();
    e.stopPropagation();
    const isValid = await validateCurrentStep();
    if (isValid) {
      if (currentStep < totalSteps) {
        setCurrentStep(prev => prev + 1);
      }
    }
  };

  const handlePrev = (e: React.MouseEvent<HTMLButtonElement>) => {
    e.preventDefault();
    e.stopPropagation();
    if (currentStep > 1) {
      setCurrentStep(prev => prev - 1);
    }
  };

  const onSubmit = async (data: FormValues) => {
    setIsSubmitting(true);
    try {
      const currentLanguage = i18n.language || 'en';
      
      // For ghost restaurants, always use delivery only
      const finalFeatures = isGhostRestaurant ? ["delivery"] : data.features_selected;
      
      const { error } = await supabase.functions.invoke("send-join-request-email", {
        body: { 
          ...data, 
          features_selected: finalFeatures,
          language: currentLanguage,
          invitation_code: (!isGhostRestaurant && codeValidation.isValid) ? data.invitation_code : null,
          invited_by_user_id: (!isGhostRestaurant && codeValidation.inviterUserId) ? codeValidation.inviterUserId : null,
          restaurant_type: data.restaurant_type,
        },
      });

      if (error) {
        if (error.message.includes("rate_limit_exceeded")) {
          toast({
            title: t("joinUs.rateLimitError"),
            description: t("joinUs.rateLimitErrorDesc"),
            variant: "destructive",
          });
        } else {
          throw error;
        }
        return;
      }

      setIsSuccess(true);
      form.reset();
      setCurrentStep(1);
      toast({
        title: t("joinUs.successTitle"),
        description: t("joinUs.successDescription"),
      });
    } catch (error: any) {
      console.error("Error submitting join request:", error);
      toast({
        title: t("common.error"),
        description: t("joinUs.submitError"),
        variant: "destructive",
      });
    } finally {
      setIsSubmitting(false);
    }
  };

  // Get localized success content from settings
  const getSuccessTitle = () => {
    const lang = i18n.language;
    if (lang === 'zh' && settings?.join_success_title_zh) return settings.join_success_title_zh;
    if (lang === 'pt' && settings?.join_success_title_pt) return settings.join_success_title_pt;
    if (settings?.join_success_title_en) return settings.join_success_title_en;
    return t("joinUs.successTitle");
  };

  const getSuccessMessage = () => {
    const lang = i18n.language;
    if (lang === 'zh' && settings?.join_success_message_zh) return settings.join_success_message_zh;
    if (lang === 'pt' && settings?.join_success_message_pt) return settings.join_success_message_pt;
    if (settings?.join_success_message_en) return settings.join_success_message_en;
    return t("joinUs.successMessage");
  };

  const contactPhone = settings?.join_contact_phone || '968815050';
  const contactEmail = settings?.contact_email;

  const progressPercent = (currentStep / totalSteps) * 100;

  if (isSuccess) {
    return (
      <div className="min-h-screen pt-20 pb-12 px-4 bg-secondary/20">
        <div className="container mx-auto max-w-2xl">
          <Card className="border-primary">
            <CardContent className="pt-12 pb-12 text-center">
              <CheckCircle2 className="w-16 h-16 text-primary mx-auto mb-4" />
              <h2 className="text-2xl font-bold text-foreground mb-4">
                {getSuccessTitle()}
              </h2>
              <p className="text-muted-foreground mb-4 max-w-md mx-auto">
                {getSuccessMessage()}
              </p>
              <div className="flex flex-col sm:flex-row items-center justify-center gap-4 mb-6">
                <a href={`tel:${contactPhone}`} className="flex items-center gap-2 text-primary font-semibold hover:underline">
                  <Phone className="w-5 h-5" />
                  <span>{contactPhone}</span>
                </a>
                {contactEmail && (
                  <a href={`mailto:${contactEmail}`} className="flex items-center gap-2 text-primary font-semibold hover:underline">
                    <Mail className="w-5 h-5" />
                    <span>{contactEmail}</span>
                  </a>
                )}
              </div>
              <Button onClick={() => setIsSuccess(false)}>
                {t("joinUs.submitAnother")}
              </Button>
            </CardContent>
          </Card>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen pt-4 pb-12 px-4 bg-secondary/20">
      <div className="container mx-auto max-w-2xl">
        <Card>
          <CardHeader className="text-center space-y-2">
            <div className="flex justify-center mb-4">
              <Store className="w-12 h-12 text-primary" />
            </div>
            <CardTitle className="text-3xl font-bold">{t("joinUs.title")}</CardTitle>
            <CardDescription className="text-base">
              {t("joinUs.description")}
            </CardDescription>
          </CardHeader>

          {/* Progress Bar */}
          <div className="px-6 pb-4">
            <div className="flex items-center justify-between mb-2">
              <span className="text-sm text-muted-foreground">
                {t("joinUs.step", { current: currentStep, total: totalSteps })}
              </span>
              <span className="text-sm font-medium">
                {Math.round(progressPercent)}%
              </span>
            </div>
            <Progress value={progressPercent} className="h-2" />
            
            {/* Step Indicators */}
            <div className="flex justify-between mt-4">
              {Array.from({ length: totalSteps }, (_, i) => i + 1).map((step) => (
                <div
                  key={step}
                  className={cn(
                    "flex items-center justify-center w-8 h-8 rounded-full text-sm font-medium transition-colors",
                    step < currentStep
                      ? "bg-primary text-primary-foreground"
                      : step === currentStep
                      ? "bg-primary text-primary-foreground ring-2 ring-primary ring-offset-2"
                      : "bg-muted text-muted-foreground"
                  )}
                >
                  {step < currentStep ? (
                    <Check className="w-4 h-4" />
                  ) : (
                    step
                  )}
                </div>
              ))}
            </div>
          </div>

          <CardContent>
            <Form {...form}>
              <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6">
                
                {/* Step 1: Restaurant Type */}
                {currentStep === 1 && (
                  <div className="space-y-6">
                    <div className="text-center mb-6">
                      <h3 className="text-lg font-semibold">{t("joinUs.selectRestaurantType")}</h3>
                      <p className="text-sm text-muted-foreground">{t("joinUs.selectRestaurantTypeDesc")}</p>
                    </div>
                    
                    <FormField
                      control={form.control}
                      name="restaurant_type"
                      render={({ field }) => (
                        <FormItem>
                          <FormControl>
                            <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                              {/* Standard Restaurant */}
                              <div
                                onClick={() => field.onChange("standard")}
                                className={cn(
                                  "relative cursor-pointer rounded-xl border-2 p-6 transition-all hover:border-primary/50",
                                  field.value === "standard"
                                    ? "border-primary bg-primary/5"
                                    : "border-muted"
                                )}
                              >
                                <div className="flex flex-col items-center text-center space-y-3">
                                  <div className={cn(
                                    "p-3 rounded-full",
                                    field.value === "standard" ? "bg-primary/10" : "bg-muted"
                                  )}>
                                    <Building2 className={cn(
                                      "w-8 h-8",
                                      field.value === "standard" ? "text-primary" : "text-muted-foreground"
                                    )} />
                                  </div>
                                  <div>
                                    <h4 className="font-semibold">{t("joinUs.standardRestaurant")}</h4>
                                    <p className="text-sm text-muted-foreground mt-1">
                                      {t("joinUs.standardRestaurantDesc")}
                                    </p>
                                  </div>
                                </div>
                                {field.value === "standard" && (
                                  <div className="absolute top-3 right-3">
                                    <Check className="w-5 h-5 text-primary" />
                                  </div>
                                )}
                              </div>

                              {/* Ghost Restaurant */}
                              <div
                                onClick={() => field.onChange("ghost")}
                                className={cn(
                                  "relative cursor-pointer rounded-xl border-2 p-6 transition-all hover:border-primary/50",
                                  field.value === "ghost"
                                    ? "border-primary bg-primary/5"
                                    : "border-muted"
                                )}
                              >
                                <div className="flex flex-col items-center text-center space-y-3">
                                  <div className={cn(
                                    "p-3 rounded-full",
                                    field.value === "ghost" ? "bg-primary/10" : "bg-muted"
                                  )}>
                                    <Ghost className={cn(
                                      "w-8 h-8",
                                      field.value === "ghost" ? "text-primary" : "text-muted-foreground"
                                    )} />
                                  </div>
                                  <div>
                                    <h4 className="font-semibold">{t("joinUs.ghostRestaurant")}</h4>
                                    <p className="text-sm text-muted-foreground mt-1">
                                      {t("joinUs.ghostRestaurantDesc")}
                                    </p>
                                  </div>
                                </div>
                                {field.value === "ghost" && (
                                  <div className="absolute top-3 right-3">
                                    <Check className="w-5 h-5 text-primary" />
                                  </div>
                                )}
                                {/* Help Button */}
                                <button
                                  type="button"
                                  onClick={(e) => {
                                    e.stopPropagation();
                                    setShowGhostInfo(true);
                                  }}
                                  className="absolute top-3 left-3 p-1 rounded-full hover:bg-muted transition-colors"
                                >
                                  <HelpCircle className="w-4 h-4 text-muted-foreground" />
                                </button>
                              </div>
                            </div>
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                  </div>
                )}

                {/* Step 2: Contact Info */}
                {currentStep === 2 && (
                  <div className="space-y-6">
                    <div className="text-center mb-6">
                      <div className="flex justify-center mb-3">
                        <User className="w-8 h-8 text-primary" />
                      </div>
                      <h3 className="text-lg font-semibold">{t("joinUs.contactInfo")}</h3>
                      <p className="text-sm text-muted-foreground">{t("joinUs.contactInfoDesc")}</p>
                    </div>

                    <FormField
                      control={form.control}
                      name="name"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>{t("joinUs.name")}</FormLabel>
                          <FormControl>
                            <Input placeholder={t("joinUs.namePlaceholder")} {...field} />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />

                    <FormField
                      control={form.control}
                      name="email"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>{t("joinUs.email")}</FormLabel>
                          <FormControl>
                            <Input type="email" placeholder={t("joinUs.emailPlaceholder")} {...field} />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />

                    <FormField
                      control={form.control}
                      name="phone"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>{t("joinUs.phone")}</FormLabel>
                          <FormControl>
                            <Input type="tel" placeholder={t("joinUs.phonePlaceholder")} {...field} />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                  </div>
                )}

                {/* Step 3: Restaurant Info */}
                {currentStep === 3 && (
                  <div className="space-y-6">
                    <div className="text-center mb-6">
                      <div className="flex justify-center mb-3">
                        <MapPin className="w-8 h-8 text-primary" />
                      </div>
                      <h3 className="text-lg font-semibold">{t("joinUs.restaurantInfo")}</h3>
                      <p className="text-sm text-muted-foreground">{t("joinUs.restaurantInfoDesc")}</p>
                    </div>

                    <FormField
                      control={form.control}
                      name="restaurant_name"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>{t("joinUs.restaurantName")}</FormLabel>
                          <FormControl>
                            <Input placeholder={t("joinUs.restaurantNamePlaceholder")} {...field} />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />

                    <FormField
                      control={form.control}
                      name="restaurant_address"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>{t("joinUs.restaurantAddress")}</FormLabel>
                          <FormControl>
                            <Input placeholder={t("joinUs.restaurantAddressPlaceholder")} {...field} />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                  </div>
                )}

                {/* Step 4: Features (only for standard restaurants) */}
                {currentStep === 4 && !isGhostRestaurant && (
                  <div className="space-y-6">
                    <div className="text-center mb-6">
                      <div className="flex justify-center mb-3">
                        <Ticket className="w-8 h-8 text-primary" />
                      </div>
                      <h3 className="text-lg font-semibold">{t("joinUs.featuresNeeded")}</h3>
                      <p className="text-sm text-muted-foreground">{t("joinUs.featuresDescription")}</p>
                    </div>

                    <FormField
                      control={form.control}
                      name="features_selected"
                      render={({ field }) => (
                        <FormItem>
                          <div className="space-y-3">
                            {featureOptions.map((feature) => {
                              const isChecked = field.value?.includes(feature);
                              
                              return (
                                <div
                                  key={feature}
                                  className="flex flex-row items-center space-x-3 space-y-0 rounded-lg border p-4 cursor-pointer hover:bg-muted/50 transition-colors"
                                  onClick={() => {
                                    if (isChecked) {
                                      field.onChange(field.value?.filter((value: string) => value !== feature));
                                    } else {
                                      field.onChange([...(field.value || []), feature]);
                                    }
                                  }}
                                >
                                  <div className={cn(
                                    "h-4 w-4 shrink-0 rounded-sm border flex items-center justify-center transition-colors",
                                    isChecked 
                                      ? "bg-primary border-primary text-primary-foreground" 
                                      : "border-primary"
                                  )}>
                                    {isChecked && <Check className="h-3 w-3" />}
                                  </div>
                                  <span className="font-normal cursor-pointer flex-1 text-sm">
                                    {t(`joinUs.feature.${feature}`)}
                                  </span>
                                </div>
                              );
                            })}
                          </div>
                          <FormMessage />
                        </FormItem>
                      )}
                    />

                    {/* Invitation Code Field - only show for standard restaurants with qrcode */}
                    <FormField
                      control={form.control}
                      name="invitation_code"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel className="flex items-center gap-2">
                            <Ticket className="w-4 h-4" />
                            {t("joinUs.invitationCode")}
                            <span className="text-muted-foreground font-normal text-xs">
                              ({t("joinUs.optional")})
                            </span>
                          </FormLabel>
                          <FormControl>
                            <div className="relative">
                              <Input 
                                placeholder={t("joinUs.invitationCodePlaceholder", "如有邀请码请输入")} 
                                {...field}
                                disabled={!isQrcodeSelected}
                                onChange={(e) => {
                                  field.onChange(e);
                                  if (isQrcodeSelected) {
                                    handleValidateCode(e.target.value);
                                  }
                                }}
                                onFocus={() => {
                                  if (!isQrcodeSelected) {
                                    toast({
                                      title: t("joinUs.qrcodeRequired", "请先勾选扫码点餐"),
                                      description: t("joinUs.qrcodeRequiredDesc", "只有选择扫码点餐功能后才能使用邀请码"),
                                    });
                                  }
                                }}
                                className={cn("pr-10", !isQrcodeSelected && "cursor-not-allowed opacity-50")}
                              />
                              {isQrcodeSelected && codeValidation.isValidating && (
                                <Loader2 className="w-4 h-4 absolute right-3 top-1/2 -translate-y-1/2 animate-spin text-muted-foreground" />
                              )}
                              {isQrcodeSelected && !codeValidation.isValidating && codeValidation.isValid === true && (
                                <Check className="w-4 h-4 absolute right-3 top-1/2 -translate-y-1/2 text-green-500" />
                              )}
                              {isQrcodeSelected && !codeValidation.isValidating && codeValidation.isValid === false && field.value && (
                                <X className="w-4 h-4 absolute right-3 top-1/2 -translate-y-1/2 text-red-500" />
                              )}
                            </div>
                          </FormControl>
                          {!isQrcodeSelected && (
                            <p className="text-xs text-muted-foreground">{t("joinUs.qrcodeRequiredHint", "需要勾选扫码点餐功能后才能填写邀请码")}</p>
                          )}
                          {isQrcodeSelected && codeValidation.isValid === true && (
                            <p className="text-xs text-green-600">{t("joinUs.invitationCodeValid", "邀请码有效")}</p>
                          )}
                          {isQrcodeSelected && codeValidation.isValid === false && field.value && (
                            <p className="text-xs text-red-600">{t("joinUs.invitationCodeInvalid", "邀请码无效")}</p>
                          )}
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                  </div>
                )}

                {/* Terms consent (show on last step) */}
                {currentStep === totalSteps && (
                  <div className="text-xs text-muted-foreground text-center pb-4">
                    {t("joinUs.consentText")}{" "}
                    <Link to="/privacy-policy" className="text-primary hover:underline">
                      {t("joinUs.privacyPolicy")}
                    </Link>{" "}
                    {t("joinUs.and")}{" "}
                    <Link to="/terms-of-service" className="text-primary hover:underline">
                      {t("joinUs.termsOfService")}
                    </Link>
                  </div>
                )}

                {/* Navigation Buttons */}
                <div className="flex justify-between gap-4 pt-4">
                  {currentStep > 1 ? (
                    <Button
                      type="button"
                      variant="outline"
                      onClick={handlePrev}
                      className="flex-1"
                    >
                      <ChevronLeft className="w-4 h-4 mr-2" />
                      {t("joinUs.prev")}
                    </Button>
                  ) : (
                    <div className="flex-1" />
                  )}
                  
                  {currentStep < totalSteps ? (
                    <Button
                      type="button"
                      onClick={handleNext}
                      className="flex-1"
                    >
                      {t("joinUs.next")}
                      <ChevronRight className="w-4 h-4 ml-2" />
                    </Button>
                  ) : (
                    <Button 
                      type="submit" 
                      className="flex-1" 
                      disabled={isSubmitting}
                    >
                      {isSubmitting && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
                      {t("joinUs.submit")}
                    </Button>
                  )}
                </div>

                {/* Official website link */}
                <div className="text-center pt-4">
                  <button
                    type="button"
                    onClick={() => openExternalUrl("https://xiaoxiong.pt/en/pages/subscription")}
                    className="inline-flex items-center gap-1 text-sm text-primary hover:underline"
                  >
                    {t("joinUs.visitOurWebsite", "访问我们的官网查看详细信息")}
                    <ExternalLink className="w-3 h-3" />
                  </button>
                </div>
              </form>
            </Form>
          </CardContent>
        </Card>
      </div>

      {/* Ghost Restaurant Info Dialog */}
      <Dialog open={showGhostInfo} onOpenChange={setShowGhostInfo}>
        <DialogContent className="sm:max-w-md">
          <DialogHeader>
            <DialogTitle className="flex items-center gap-2">
              <Ghost className="w-5 h-5 text-primary" />
              {t("joinUs.ghostRestaurantInfoTitle")}
            </DialogTitle>
            <DialogDescription asChild>
              <div className="space-y-4 pt-4">
                <p>{t("joinUs.ghostRestaurantInfo1")}</p>
                <div className="space-y-2">
                  <h4 className="font-medium text-foreground">{t("joinUs.ghostRestaurantFeatures")}</h4>
                  <ul className="list-disc list-inside space-y-1 text-sm">
                    <li>{t("joinUs.ghostRestaurantFeature1")}</li>
                    <li>{t("joinUs.ghostRestaurantFeature2")}</li>
                    <li>{t("joinUs.ghostRestaurantFeature3")}</li>
                  </ul>
                </div>
                <p className="text-sm">{t("joinUs.ghostRestaurantInfo2")}</p>
              </div>
            </DialogDescription>
          </DialogHeader>
          <div className="flex justify-end pt-4">
            <Button onClick={() => setShowGhostInfo(false)}>
              {t("common.gotIt")}
            </Button>
          </div>
        </DialogContent>
      </Dialog>
    </div>
  );
};

export default JoinUs;
